.row {
  display: flex;
  align-items: center;
  justify-content: center;
}

.row > button {
  margin-left: 4px;
  margin-right: 8px;
  background-color: rgb(112, 76, 182);
}

.row:not(:last-child) {
  margin-bottom: 16px;
}

.value {
  font-size: 78px;
  padding-left: 16px;
  padding-right: 16px;
  margin-top: 2px;
  font-family: 'Courier New', Courier, monospace;
}

.button {
  appearance: none;
  background: none;
  font-size: 32px;
  outline: none;
  border: 2px solid transparent;
  color: rgb(112, 76, 182);

  padding-left: 12px;
  padding-right: 12px;
  padding-bottom: 4px;
  cursor: pointer;
  background-color: rgba(112, 76, 182, 0.1);
  border-radius: 2px;
  transition: all 0.15s;
}

.textbox {
  font-size: 32px;
  padding: 2px;
  width: 64px;
  text-align: center;
  margin-right: 4px;
}



.asyncButton {
  composes: button;
  position: relative;
}

.asyncButton:after {
  content: '';
  background-color: rgba(112, 76, 182, 0.15);
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  transition: width 1s linear, opacity 0.5s ease 1s;
}

.asyncButton:active:after {
  width: 0%;
  opacity: 1;
  transition: 0s;
}



button.buttonThermite {
  appearance: none;
  background: none;
  font-size: 32px;
  padding-left: 12px;
  padding-right: 12px;
  padding-bottom: 4px;
  outline: none;
  border: 2px solid transparent;
  color: rgb(112, 76, 182);
  background-color: rgb(112, 76, 182);
  padding-bottom: 4px;
  cursor: pointer;
  font-weight: 600;
  border-radius: 2px;
  transition: all 0.15s;

  margin-left: 4px;
  margin-right: 8px;
  animation: buttonThermite 1 6s ease-in-out;

}

@media (prefers-reduced-motion: no-preference) {
  .buttonThermite {
    animation: buttonThermite 1 6s ease-in-out;
  }
}

@keyframes buttonThermite {
	0% {
    color: rgb(212, 212, 212);
		background-color: rgb(212, 212, 212);
	}
  60% {
    color: rgb(212, 212, 212);
		background-color: rgb(212, 212, 212);
	}
	100% {
    color: rgb(112, 76, 182);
		background-color: rgb(112, 76, 182);
	}
}


